body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(131, 58, 180);
  background: linear-gradient(0deg, rgba(131, 58, 180, 1) 0%, rgba(29, 195, 253, 1) 50%, rgba(252, 176, 69, 1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container h1 {
  display: flex;
  align-items: center;
  justify-content: center;
}

.container h1 span {
  color: rgb(255, 47, 47);
}

.container p {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  text-align: center;
  font-weight: 600;
}
/* .container p span{
  font-weight: 700;
  color: indigo;
} */

.container .main-container {
  display: block;
  align-items: center;
  justify-content: center;
  /* border: 2px solid black; */
  /* padding: 10px; */
  margin: 10px 10%;
  height: 740px;
}

.main-container .gen-form {
  display: block;
  justify-content: center;
  align-items: center;
  margin: 10px 10px;

  /* border: 1px solid red; */

}

.main-container .gen-form input {
  height: 30px;
  font-size: 20px;
  margin: 10px 30%;
  width: 456px;
  border: none;
  padding-left: 14px;

  transition: 0.3s all ease;
}

.main-container .gen-form input:focus {
  outline: none;
  border-radius: 12px;
}

.main-container button {
  margin: 10px 44%;
  width: 130px;
  height: 40px;
  font-size: 20px;
  transition: 0.3s all ease-in-out;

  cursor: pointer;
  background-color: black;
  color: white;

  border-radius: 14px;
}

.main-container button:hover {
  scale: 1.1;
  border-radius: 0px;
}

.main-container .mainImageBox {
  /* border: 1px solid red; */
  margin: 10px 10%;
}

.main-container .mainImageBox .result-image img {
  margin: 0px 30%;
  max-width: 340px;
  height: auto;
  border-radius: 16px;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;

  color: aliceblue;
  padding: 0px 10px;

  background: black;
}

.footer p a {
  text-decoration: none;
  color: darkorchid;
}

@media screen and (max-width: 1300px) {
  .main-container .gen-form input {
    margin: 10px 30%;
    width: 370px;
    transition: 0.3s all ease;
  }


}

@media screen and (max-width: 990px) {
  .main-container .gen-form input {
    margin: 10px 27%;
    width: 300px;
    transition: 0.3s all ease;
  }

  .main-container button {
    margin: 10px 40%;
  }

  .main-container .mainImageBox .result-image img {
    margin: 0px 22%;
  }
}

@media screen and (max-width: 760px) {
  .main-container .gen-form input {
    margin: 10px 22%;
    width: 280px;
    transition: 0.3s all ease;
  }

  .container .main-container {
    margin: 10px 6%;
  }

  .main-container .mainImageBox .result-image img {
    margin: 0px 15%;
    max-width: 340px;
    height: auto;
  }
}

@media screen and (max-width: 530px) {
  .main-container .gen-form input {
    margin: 10px 14%;
    width: 280px;
    transition: 0.3s all ease;
  }

  .main-container button {
    margin: 10px 35%;
  }

  .main-container .mainImageBox .result-image img {
    margin: 0px 10%;
    max-width: 290px;
    height: auto;
  }
}

@media screen and (max-width: 490px) {
  .main-container .gen-form input {
    margin: 10px 11%;
    width: 250px;
    transition: 0.3s all ease;
  }

  .main-container button {
    margin: 10px 32%;
  }

  .main-container .mainImageBox .result-image img {
    margin: 0px 2%;
    max-width: 260px;
    height: auto;
  }
}

@media screen and (max-width: 350px) {
  .main-container .gen-form input {
    margin: 10px 0%;
    width: 250px;
    transition: 0.3s all ease;
  }

  .main-container button {
    margin: 10px 23%;
  }

  .container .main-container {
    margin: 10px 2%;
  }

  .main-container .mainImageBox .result-image img {
    margin: 0px 6%;
    max-width: 220px;
    height: auto;
  }
}

@media screen and (max-width: 300px) {
  .main-container .gen-form input {
    margin: 10px -3%;
    height: 20px;
    font-size: 14px;
  }

  .main-container .mainImageBox .result-image img {
    margin: 0px -1%;
  }
}